<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">命案嫌疑人分布</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="8">
                <techo-card resizeEvent="bottomLeftCard" :maxCard="true">
                    <div slot="header">近5年发案数</div>
                    <div id="LeftBar" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="bottomMidCard" :maxCard="true">
                    <div slot="header">近5年侦查终结数</div>
                    <div id="MidCar" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8" >
                <techo-card resizeEvent="bottomRightCard" :maxCard="true">
                    <div slot="header">籍贯</div>
                    <div id="RightCard" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/district_check_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 命案嫌疑人分布
      topBarOptions: {},
      topBarChart: null,
      // 近5年发案数
      bottomLeftOptions: {},
      bottomLeftChart: null,
      // 近5年侦查终结数
      bottomMidOptions: {},
      bottomMidChart: null,
      // 籍贯
      bottomRightOptions: {},
      bottomRightChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 命案嫌疑人分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 近5年发案数
    this.bottomLeftChart = this.$echarts.init(
      this.$el.querySelector("#LeftBar")
    );
    // 近5年侦查终结数
    this.bottomMidChart = this.$echarts.init(this.$el.querySelector("#MidCar"));
    // 籍贯
    this.bottomRightChart = this.$echarts.init(
      this.$el.querySelector("#RightCard")
    );

    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getImpArea(val.data.rawdata.code);
        this.getImpPro(val.data.rawdata.code);
        this.getAreaType(val.data.rawdata.code);
        this.getResAss(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("bottomLeftCard", () => {
      that.bottomLeftChart.resize();
    });
    this.$bus.$on("bottomMidCard", () => {
      that.bottomMidChart.resize();
    });
    this.$bus.$on("bottomRightCard", () => {
      that.bottomRightChart.resize();
    });
  },
  created() {
    /** 命案嫌疑人分布 */
    this.getImpArea();
    /** 近5年发案数 */
    this.getImpPro();
    /** 近5年侦查终结数 */
    this.getAreaType();
    /** 籍贯 */
    this.getResAss();
  },

  methods: {
    //命案嫌疑人分布
    getImpArea(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "PeaceMurCaseInfo",
       
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityNames(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "发案数";
        this.topBarOptions.legend.data = ["发案数"];
        that.topBarOptions.series = [
          {
            name: "发案数",
            type: "bar",
            itemStyle: {
              normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
            },
            barMaxWidth: 80,
            data: []
          }
        ];
        _.map(data, item => {
          that.topBarOptions.xAxis.data.push(item.name);
          that.topBarOptions.series[0].data.push({
            rawdata: item,
            value: item.count
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },

    getImpPro() {
      let that = this;
      this.bottomLeftOptions = _.cloneDeep(barOptions);
      service.requestsTimeYear(this.DISCODE, data => {
        this.bottomLeftOptions.yAxis.name = "近5年侦查终结数";
        this.bottomLeftOptions.legend.data = ["案件数"];
        that.bottomLeftOptions.series = [
          {
            name: "案件数",
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[0]
              }
            },
            barMaxWidth: 80,
            data: _.values(data)
          }
        ];
        that.bottomLeftOptions.xAxis.data = _.keys(data);
        this.bottomLeftChart.setOption(that.bottomLeftOptions);
        this.bottomLeftChart.resize();
      });
    },
    // 近5年侦查终结数
    getAreaType() {
      let that = this;
      this.bottomMidOptions = _.cloneDeep(barOptions);
      service.requesteTimeYear(this.DISCODE, data => {
        this.bottomMidOptions.yAxis.name = "近5年侦查终结数";
        this.bottomMidOptions.legend.data = ["案件数"];
        that.bottomMidOptions.series = [
          {
            name: "案件数",
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[0]
              }
            },
            barMaxWidth: 80,
            data: _.values(data)
          }
        ];
        that.bottomMidOptions.xAxis.data = _.keys(data);
        this.bottomMidChart.setOption(that.bottomMidOptions);
        this.bottomMidChart.resize();
      });
    },
    // 籍贯
    getResAss(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "PeaceMurSuspect",
        fieldName: "nativePlace"
      };
      let that = this;
      this.bottomRightOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.bottomRightOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.bottomRightOptions.title.text = "省份";
        that.bottomRightOptions.series[0].name = "命案犯罪嫌疑人";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.bottomRightOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.bottomRightOptions.series[0].radius = "45%";
        that.bottomRightOptions.series[0].center = ["65%", "50%"];
        that.bottomRightChart.setOption(that.bottomRightOptions);
        that.bottomRightChart.resize();
      });
    },

    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getImpArea(districtCode);
      this.getImpPro(districtCode);
      this.getAreaType(districtCode);
      this.getResAss(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 23rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 24.2rem);
    width: 100%;
    .el-col-8 {
      height: 100%;
    }
  }
}
</style>